import { createBasket } from '../api'

export function setBasketToken(token) {
  localStorage.setItem('basketToken', token)
}

export async function getBasketToken() {
  const token = localStorage.getItem('basketToken')
  if (!token) {
    const basket = await createBasket()
    const responseToken = basket.id
    setBasketToken(responseToken)
    return responseToken
  }
  return token
}